import ICONJSON from '../../static/json/icon.js';
import WaCard from '../../components/Card.vue';
import NativeBar from '../../components/NativeBar.vue';
export default {
	components: {
		WaCard,
		NativeBar
	},
	data() {
		return {
			icon: [],
			example: "<text class='icon-favorfill'></text>"
		};
	},
	methods: {
		copyClass(name) {
			uni.setClipboardData({
				data: name,
				success: function() {
					wx.showToast({
						title: '复制',
						duration: 1000,
						mask: true
					})
				}
			});
		}
	},
	mounted() {
		this.icon = ICONJSON;
	}
}
